.Title {
  background: transparent;
  height: 50px;
  text-align: center;
  width:100vw;
  font-size: 1rem;
  margin-top: 25px;
  margin-bottom: 75px;
}

.NavbarItems {	
  background: #f4f4f4;	
  height: 80px;	
  display: flex;	
  justify-content: left;	
  align-items: left;	
  font-size: 1.2rem;	
  width: 75%;	
  text-align: left;	
  margin-left: 25%;	
}	
.navbar-logo {	
  color: rgb(226, 23, 23);	
  justify-self: start;	
  margin-left: 0px;	
  cursor: pointer;	
}	
.logo {	
  width: 230px;	
}	
.nav-menu {	
  display: grid;	
  grid-template-columns: repeat(5, auto);	
  grid-gap: 50px;	
  list-style: none;	
  text-align: left;	
  justify-content: end;	
  padding-top: 1rem;	
  padding-left: 0rem;	
  font-size: 25px;	
}	
